import {
  ListItem,
  ListItemButton,
  ListItemText,
  Stack,
  Toolbar,
  Typography,
  Box,
  Drawer,
  IconButton,
  Divider,
  List,
  ListItemIcon,
  AppBarProps as MuiAppBarProps,
  Grid,
  Button,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import { Link as Lin } from "react-router-dom";
import { useState, useEffect } from "react";
import {
  createMuiTheme,
  styled,
  ThemeProvider,
  useTheme,
} from "@mui/material/styles";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import MuiAppBar from "@mui/material/AppBar";
import LocalPhoneIcon from "@mui/icons-material/LocalPhone";
import Link from "@mui/material/Link";
import FacebookIcon from "@mui/icons-material/Facebook";
import GitHubIcon from "@mui/icons-material/GitHub";
import EmailIcon from "@mui/icons-material/Email";
import profileImg from "./pics/Profile.png";
const drawerWidth = 250;

const Main = styled("main", { shouldForwardProp: (prop) => prop !== "open" })<{
  open?: boolean;
}>(({ theme, open }) => ({
  flexGrow: 1,
  padding: theme.spacing(3),
  transition: theme.transitions.create("margin", {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  marginLeft: `-${drawerWidth}px`,
  ...(open && {
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  }),
}));

interface AppBarProps extends MuiAppBarProps {
  open?: boolean;
}

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== "open",
})<AppBarProps>(({ theme, open }) => ({
  transition: theme.transitions.create(["margin", "width"], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: `${drawerWidth}px`,
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const DrawerHeader = styled("div")(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
  justifyContent: "flex-end",
}));

const customTheme = createMuiTheme({
  palette: {
    secondary: {
      main: "#000000",
      contrastText: "#ffffff",
    },
  },
});
type props = {
  openDrawer?: boolean,
  setOpenDrawer?: (value:boolean) => void
}
const AppBarNav:React.FC<props> = ({openDrawer, setOpenDrawer}) => {
  const theme = useTheme();

  const navItems = [
    {
      text: "Home",
    },
    {
      text: "Skill",
    },
  ];

  const contractList = [
    {
      text: "086-9181733",
      href: "",
      icon: <LocalPhoneIcon />,
    },
    {
      text: "thiradon_th@cmu.ac.th",
      href: "",
      icon: <EmailIcon />,
    },
    {
      text: "Facebook",
      href: "https://www.facebook.com/teeradon.thaiyanto",
      icon: <FacebookIcon />,
    },
    {
      text: "GitHub",
      href: "https://github.com/thiradonTH",
      icon: <GitHubIcon />,
    },
  ];

  const [currentPage, setCurrentPage] = useState("Home")

  useEffect(() => {
    console.log(currentPage)
  }, [currentPage])



  const handleDrawerOpen = () => {
    setOpenDrawer?.(true);
  };

  const handleDrawerClose = () => {
    setOpenDrawer?.(false);
  };

  return (
    <ThemeProvider theme={customTheme}>
      <Box sx={{ display: "flex" }}>
        <AppBar position="fixed" open={openDrawer} color={"secondary"}>
          <Toolbar>
            <DrawerHeader>
              <IconButton
                color="inherit"
                aria-label="open drawer"
                onClick={handleDrawerOpen}
                edge="end"
                sx={{ mr: 2, ...(openDrawer && { display: "none" }) }}
              >
                <MenuIcon />
              </IconButton>
            </DrawerHeader>

            <Stack direction="row" >
              {navItems.map((item, index) => {
                const { text } = item;
                return (
                  <ListItem key={text} sx={{padding:0}}>
                    <ListItemButton sx={{ textAlign: "center", backgroundColor: currentPage=== text ? "white" : "black" , padding: 2, marginRight:0}}>
                      <ListItemText sx= {{ }}
                      primary = {<Lin
                            onClick = {() => {
                              setCurrentPage(text)
                              // console.log("Yeah")
                            }}
                            to={`/${text}`}
                            style={{ textDecoration: "none" }}
                            
                          >
                            <Typography
                              sx={{
                                fontWeight: "bold",
                    
                              }} 
                              color= {currentPage=== text ? "black" : "white"}
                            >
                              {text}
                            </Typography>
                            
                          </Lin>}
                      />
                          
                       
      
                    </ListItemButton>
                  </ListItem>
                );
              })}
            </Stack>
          </Toolbar>
        </AppBar>
        <Drawer
          sx={{
            width: drawerWidth,
            flexShrink: 0,
            "& .MuiDrawer-paper": {
              width: drawerWidth,
              boxSizing: "border-box",
            },
          }}
          variant="persistent"
          anchor="left"
          open={openDrawer}
        >
          <Grid container columnSpacing={2}>
            <Grid item md={6} >
              <Typography variant="h5" sx={{justifyContent:"center", display:"flex", mt:"20px"}}>RESUME</Typography>
            </Grid>
            <Grid item md={6}>
              <DrawerHeader>
                <IconButton onClick={handleDrawerClose}>
                  {theme.direction === "ltr" ? (
                    <ChevronLeftIcon />
                  ) : (
                    <ChevronRightIcon />
                  )}
                </IconButton>
              </DrawerHeader>
            </Grid>
          </Grid>

          <Divider />
          <img src={profileImg} style={{ padding: "6px", marginTop:"10%" }} />
          <Divider sx={{mt:"10%"}}/>
          <Typography
            variant="h5"
            textAlign="center"
            padding="5px"
            justifyContent="center"
            sx={{ fontWeight: "bold", mt: "6px" }}
          >
            Contract
          </Typography>
          <List>
            {contractList.map((item, index) => {
              const { text, href, icon } = item;
              return (
                 index > 1 ? 
                  (<Link
                    href={href}
                    target="_blank"
                    color="inherit"
                    underline="none"
                   ><ListItem button key={text}>
                      {icon && <ListItemIcon>{icon}</ListItemIcon>}
                      <ListItemText primary={text} />
                    </ListItem>
                    </Link> ): 
                  (<ListItem button key={text}>
                      {icon && <ListItemIcon>{icon}</ListItemIcon>}
                      <ListItemText primary={text} />
                   </ListItem>)      
              );
            })}
          </List>
        </Drawer>
      </Box>
    </ThemeProvider>
  );
};

export default AppBarNav;
