import {
  Grid,
  Paper,
  CardContent,
  Typography,
  Divider,
  Popover,
} from "@mui/material";
import Motion from "./Motion";
import { useState, MouseEvent } from "react";
import { motion } from "framer-motion";
import { useOutletContext } from "react-router-dom";
const Skill:React.FC<{}> = () => {

  const isOpenDrawer = useOutletContext<boolean>();

  const [anchorEl1, setAnchorEl1] = useState<HTMLElement | null>(null);

  const handlePopoverOpen1 = (event: MouseEvent<HTMLElement>) => {
    setAnchorEl1(event.currentTarget);
  };

  const handlePopoverClose1 = () => {
    setAnchorEl1(null);
  };

  const open1 = Boolean(anchorEl1);

  const [anchorEl2, setAnchorEl2] = useState<HTMLElement | null>(null);

  const handlePopoverOpen2 = (event: MouseEvent<HTMLElement>) => {
    setAnchorEl2(event.currentTarget);
  };

  const handlePopoverClose2 = () => {
    setAnchorEl2(null);
  };

  const open2 = Boolean(anchorEl2);

  const [anchorEl3, setAnchorEl3] = useState<HTMLElement | null>(null);

  const handlePopoverOpen3 = (event: MouseEvent<HTMLElement>) => {
    setAnchorEl3(event.currentTarget);
  };

  const handlePopoverClose3 = () => {
    setAnchorEl3(null);
  };

  const open3 = Boolean(anchorEl3);

  const [anchorEl4, setAnchorEl4] = useState<HTMLElement | null>(null);

  const handlePopoverOpen4 = (event: MouseEvent<HTMLElement>) => {
    setAnchorEl4(event.currentTarget);
  };

  const handlePopoverClose4 = () => {
    setAnchorEl4(null);
  };

  const open4 = Boolean(anchorEl4);

  const [anchorEl5, setAnchorEl5] = useState<HTMLElement | null>(null);

  const handlePopoverOpen5 = (event: MouseEvent<HTMLElement>) => {
    setAnchorEl5(event.currentTarget);
  };

  const handlePopoverClose5 = () => {
    setAnchorEl5(null);
  };

  const open5 = Boolean(anchorEl5);

  const [anchorEl6, setAnchorEl6] = useState<HTMLElement | null>(null);

  const handlePopoverOpen6 = (event: MouseEvent<HTMLElement>) => {
    setAnchorEl6(event.currentTarget);
  };

  const handlePopoverClose6 = () => {
    setAnchorEl6(null);
  };

  const open6 = Boolean(anchorEl6);

  const [anchorEl7, setAnchorEl7] = useState<HTMLElement | null>(null);

  const handlePopoverOpen7 = (event: MouseEvent<HTMLElement>) => {
    setAnchorEl7(event.currentTarget);
  };

  const handlePopoverClose7 = () => {
    setAnchorEl7(null);
  };

  const open7 = Boolean(anchorEl7);

  const skillList1 = [
    {
      text: "C++",
      popoverId: "C++",
      popoverContent: "TOI 15",
      ancohorEL: anchorEl1,
      PopoverOpen: handlePopoverOpen1,
      PopoverClose: handlePopoverClose1,
      open: open1,
    },
    {
      text: "Python",
      popoverId: "Python",
      popoverContent: "Basic Programming",
      ancohorEL: anchorEl2,
      PopoverOpen: handlePopoverOpen2,
      PopoverClose: handlePopoverClose2,
      open: open2,
    },
    {
      text: "Java",
      popoverId: "Java",
      popoverContent: "Object Oriented Programming",
      ancohorEL: anchorEl3,
      PopoverOpen: handlePopoverOpen3,
      PopoverClose: handlePopoverClose3,
      open: open3,
    },
    {
      text: "Vue",
      popoverId: "Vue",
      popoverContent: "Frontend Classroom Reservation Website",
      ancohorEL: anchorEl4,
      PopoverOpen: handlePopoverOpen4,
      PopoverClose: handlePopoverClose4,
      open: open4,
    },
  ];

  const skillList2 = [
    {
      text: "React",
      popoverId: "React",
      popoverContent: "RESUME Website",
      ancohorEL: anchorEl5,
      PopoverOpen: handlePopoverOpen5,
      PopoverClose: handlePopoverClose5,
      open: open5,
    },
    {
      text: "Scala",
      popoverId: "Scala",
      popoverContent: "Plactice in HackerRank",
      ancohorEL: anchorEl6,
      PopoverOpen: handlePopoverOpen6,
      PopoverClose: handlePopoverClose6,
      open: open6,
    },
    {
      text: "Dokuwiki",
      popoverId: "Dokuwiki",
      popoverContent: "Curriculum Website",
      ancohorEL: anchorEl7,
      PopoverOpen: handlePopoverOpen7,
      PopoverClose: handlePopoverClose7,
      open: open7,
    },
  ];

  return (
    <>
    
      <Paper elevation={3} sx={{ width: isOpenDrawer ? "80%" : "96%", height: "100%",float: "right" ,marginRight:"2rem"}}>
        <CardContent sx={{}}>
          <Typography variant="h5">Skill</Typography>
          <Divider />
          <div className="skillContainer">
            <div className="hexagonArea first">
              {skillList1.map((item, index) => {
                const {
                  text,
                  popoverId,
                  popoverContent,
                  ancohorEL,
                  PopoverOpen,
                  PopoverClose,
                  open,
                } = item;
                return (
                  <motion.div
                    className="hexagon"
                    whileHover={{ scale: 0.8 }}
                    transition={{
                      duration: 0.3,
                    }}
                    aria-owns={open ? `${popoverId}` : undefined}
                    aria-haspopup="true"
                    onMouseEnter={PopoverOpen}
                    onMouseLeave={PopoverClose}
                  >
                    <Typography variant="h5">{text}</Typography>
                    <Popover
                      id={popoverId}
                      sx={{
                        pointerEvents: "none",
                      }}
                      open={open}
                      anchorEl={ancohorEL}
                      anchorOrigin={{
                        vertical: "bottom",
                        horizontal: "left",
                      }}
                      transformOrigin={{
                        vertical: "top",
                        horizontal: "left",
                      }}
                      onClose={PopoverClose}
                      disableRestoreFocus
                    >
                      <Typography>{popoverContent}</Typography>
                    </Popover>
                  </motion.div>
                );
              })}
            </div>
            <div className="hexagonArea second">
            {skillList2.map((item, index) => {
                const {
                  text,
                  popoverId,
                  popoverContent,
                  ancohorEL,
                  PopoverOpen,
                  PopoverClose,
                  open,
                } = item;
                return (
                  <motion.div
                    className="hexagon"
                    whileHover={{ scale: 0.8 }}
                    transition={{
                      duration: 0.3,
                    }}
                    aria-owns={open ? `${popoverId}` : undefined}
                    aria-haspopup="true"
                    onMouseEnter={PopoverOpen}
                    onMouseLeave={PopoverClose}
                  >
                    <Typography variant="h5">{text}</Typography>
                    <Popover
                      id={popoverId}
                      sx={{
                        pointerEvents: "none",
                      }}
                      open={open}
                      anchorEl={ancohorEL}
                      anchorOrigin={{
                        vertical: "bottom",
                        horizontal: "left",
                      }}
                      transformOrigin={{
                        vertical: "top",
                        horizontal: "left",
                      }}
                      onClose={PopoverClose}
                      disableRestoreFocus
                    >
                      <Typography>{popoverContent}</Typography>
                    </Popover>
                  </motion.div>
                );
              })}
            </div>
          </div>
        </CardContent>
      </Paper>
    </>
  );
};

export default Skill;
