import {
  CardContent,
  Divider,
  Grid,
  ListItem,
  Paper,
  Typography,
  List,
} from "@mui/material";
import { useOutletContext } from "react-router-dom";
import oympic from "./pics/oympic.jpg";

const Home: React.FC<{}> = () => {
  const isOpenDrawer = useOutletContext<boolean>();
  // console.log("isOpenDrawer = " + isOpenDrawer)
  return (
    <>
      <Grid container columnSpacing={3}>
        <Grid item md={isOpenDrawer ? 2 : 0}></Grid>
        <Grid item md={6} sx={{ marginLeft: isOpenDrawer ? 0 : "3rem", maxHeight:"100%" }}>
          <Paper elevation={3} sx={{ margin: "4px" }}>
            <CardContent>
              <Typography variant="h5">Thiradon Thaiyanto</Typography>
              <Divider />
              <Typography variant="body1" mt="5px">
                Hello, I'm a student at Chiang Mai University, Faculty of
                Science, Computer Science. I was born on August 28, 2001. I've
                tried a lot of things and I'm not particularly interested in
                anything. Having a hobby is playing games. I'm good at
                plobrem-solving programming and practicing other skills like
                creating Website, Object-Oriented progamming and new technology.{" "}
              </Typography>
            </CardContent>
          </Paper>
          <Paper elevation={3} sx={{ margin: "4px", marginTop: "14px" }}>
            <CardContent>
              <Typography variant="h5">Education</Typography>
              <Divider />
              <Typography variant="h4" mt="5px">
                2017-2019 High school
              </Typography>
              <Typography variant="body1">Dara Academy</Typography>
              <Typography variant="body1">GPA 3.90</Typography>
              <Typography variant="h4" mt="5px">
                2020-2023 Bachelor's Degree (Progress)
              </Typography>
              <Typography variant="body1">
                Chiang Mai University, Faculty of Science, Computer Science
              </Typography>
              <Typography variant="body1">GPA(secondary year) 3.71</Typography>
            </CardContent>
          </Paper>
        </Grid>
        <Grid item md={isOpenDrawer ? 4 : 5}>
          <Paper
            elevation={3}
            sx={{ margin: "4px", marginRight: "0", width: "96%" }}
          >
            <CardContent>
              <Typography variant="h5">Experience</Typography>
              <Divider />
              <List
                sx={{ maxHeight: "400px", position: "relative", overflow: "auto" }}
              >
                <ListItem>
                  <Typography variant="body1" mt="5px">
                    - Bronze Medal in Thailand Olympic Infomatics 15th.
                  </Typography>
                </ListItem>
                <ListItem>
                  <img src={oympic} style={{ maxWidth: "100%" }} />
                </ListItem>
              </List>
            </CardContent>
          </Paper>
        </Grid>
      </Grid>
    </>
  );
};

export default Home;
